.gallery {
  max-width: calc(100% - 64px);
  grid-gap: 24px;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  justify-content: center;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  padding: 30px;
}

.gallery__item p {
  margin-top: 0;
  margin-bottom: 8px;
}

.gallery__thumb {
  height: 200px;
  margin-bottom: 16px;
}

.gallery__item img {
  object-fit: cover;
  max-width: 100%;
  height: 100%;
}

